import React from "react";
import styles from "Assets/Stylesheets/SCSS/Components/IntroductionBanner.module.scss";
export default function IntroductionBanner() {
  const [atView, setAtView] = React.useState(false);

  return (
    <div className={styles.container}>
      {atView ? (
        <Content />
      ) : (
        <PlaceHolder
          setView={() => {
            setAtView(true);
          }}
        />
      )}

      <div className={styles.overlay}></div>
    </div>
  );
}
const Content = () => {
  return (
    <div className={styles.content}>
      <div className={styles.title}>
        <h2>Introduce</h2>
        <div className={styles.name}>
          <h1>Green Purchasing</h1>
          <h1>Flatform</h1>
        </div>
      </div>
      <div className={styles.paragraph}>
        <p>XANHHUB, a con tinh thn ca nhng chng sinh vin nm nht v nm 2
        ca i hc Bch khoa H Ni.</p>
         <p>XanhHUB c xy dng vi s mnh thc
        y xu hng tiu dng xanh  Vit Nam. Chng ti n vi cuc thi 
        em v 20 triu VND v em li cho cng ng 20 triu USD cng nhiu gi
        tr to ln khc!</p>
      </div>
    </div>
  );
};
const PlaceHolder = ({ setView }) => {
  const Ref = React.useRef(null);
  const [offset, setOffset] = React.useState(0);
  React.useEffect(() => {
    const handler = (e) => {
      if (Ref.current) {
        const { bottom, top } = Ref.current.getBoundingClientRect();

        setOffset(Math.max(150 - bottom / 5, 0));
        const h = window.innerHeight / 3;

        if (top < h) {
          setView();
        }
      }
    };
    window.addEventListener("scroll", handler);

    return () => {
      window.removeEventListener("scroll", handler);
    };
  }, [Ref,setView]);
  return (
    <div className={styles.content}>
      <h2 style={{ top: offset }} ref={Ref} className={styles.PlaceHolder}>
        Scroll down
      </h2>
    </div>
  );
};
